.underlined-field {
  color: green;
  font-weight: 500;
  padding: 0.25rem 0;

  &:hover {
    text-decoration: underline;
  }

  &.disabled-text {
    color: #ccc;
    cursor: not-allowed;
    &:hover {
      color: #ccc;
      text-decoration: underline;
    }
  }
}

.dropdown-panel {
  min-width: 250px;
}

.condition-item {
  display: flex;
  justify-content: space-between;
  padding-top: calc(var(--padding) / 2);
}

.condition-editor {
  .field {
    margin-bottom: 1rem;

    label {
      display: block;
      margin-bottom: 0.5rem;
      font-weight: 500;
    }
  }

  .field-checkbox {
    margin-bottom: 0.5rem;
  }

  .checklist {
    max-height: 200px;
    overflow-y: auto;
    border: 1px solid #ced4da;
    border-radius: 4px;
    padding: 8px;
  }
}

.condition-list {
  .condition-items {
    .condition-item {
      .condition-display {
        border: 1px solid;
        transition: all 0.2s;

        &:hover {
          box-shadow: 0 0 0 0.2rem rgba(63, 81, 181, 0.1);
        }

        .condition-text {
          line-height: 1.5;
        }
      }
    }
  }

  .empty-conditions {
    border-style: dashed;
    color: #6c757d;
  }

  .add-condition-section {
    margin-top: 0.5rem;
  }

  .condition-dropdown {
    h3 {
      font-size: 1rem;
      margin-top: 0;
    }
  }
}

.p-error {
  font-size: 0.875rem;
}

:host {
  --p-treeselect-background: var(--p-tree-background) !important;
}
